<template>

  <v-content>
    <v-container fluid grid-list-sm>
      <v-layout
        row
        wrap
      >
        <v-flex xs12 v-if="selectedZone">
          <app-colorpicker :picker="selectedColorPicker">
            <div class="mb-2 headline menucolor--text">{{ selectedZone.name }}</div>
          </app-colorpicker>
        </v-flex>

        <v-flex xs12>
          <app-white-night></app-white-night>
        </v-flex>

        <v-flex xs12>
          <app-sbh></app-sbh>
        </v-flex>

        <v-flex xs12>
          <app-on-off></app-on-off>
        </v-flex>

      </v-layout>
    </v-container>
  </v-content>

</template>

<script>
  import Colorpicker from '@/components/colors/Colorpicker';
  import Sbh from '@/components/basics/Sbh';
  import WhiteNight from '@/components/basics/WhiteNight';
  import OnOff from '@/components/basics/OnOff';

  import { mapGetters } from 'vuex';

  export default {
    components: {
      appColorpicker: Colorpicker,
      appSbh: Sbh,
      appWhiteNight: WhiteNight,
      appOnOff: OnOff
    },
    data () {
      return {
        colorPickers: ['Slider', 'Compact', 'Swatch', 'Photoshop', 'Chrome'],
      }
    }, computed: {
      ...mapGetters(['selectedZone', 'selectedColorPicker'])
    },
  };
</script>



