<template>

  <v-content style="padding: 0px">

    <v-card>
      <v-card-text>
        <v-layout row wrap align-baseline justify-space-around>

          <v-flex
            xs6
            class="text-xs-right text-sm-center"
          >
            <v-btn
              :value="getZonesDp('on')"
              @click="setZonesDp({dp: 'on', event: true, delay: false})"
              success
              color="buttonon"
              dark
            >On
            </v-btn>
          </v-flex>

          <v-flex
            xs6
            class="text-xs-left text-sm-center"
          >
            <v-btn
              :value="getZonesDp('off')"
              @click="setZonesDp({dp: 'off', event: true, delay: false})"
              error
              color="buttonoff"
              dark
            >Off
            </v-btn>
          </v-flex>

        </v-layout>
      </v-card-text>
    </v-card>

  </v-content>

</template>


<script>
  import { mapGetters, mapActions } from 'vuex';

  export default {
    data () {
      return {};
    }, computed: {
      ...mapGetters([
        'zones', 'getZonesDp'])
    }, methods: {
      ...mapActions(['setZonesDp']),
      refreshColor () {
        this.setZonesDp({dp: 'on', event: true, delay: false});

        this.setZonesDp({dp: 'rgb', event: this.getZonesDp('rgb'), delay: false});
      },
    },
  };

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



