<template>
  <v-content
    v-if="selectedZone.type!=='white'"
    style="padding: 0px"
  >
    <v-card>
      <v-card-text>
        <v-layout row wrap>

          <v-flex xs12 v-if="selectedMode!=='appKelvin' && selectedMode!=='appModes' && selectedZone.type==='fullColor'">
            <v-slider
              color="slidercolor"
              :label="labelS"
              :min=1
              :max=100
              thumb-label
              :value="getZonesDp('saturation')"
              @input="setZonesDp({dp: 'saturation', event: $event, delay: 50})"
            ></v-slider>
          </v-flex>

          <v-flex xs12>
            <v-slider
              color="slidercolor"
              :label="labelB"
              :min=1
              :max=100
              thumb-label
              :value="getZonesDp('brightness')"
              @input="setZonesDp({dp: 'brightness', event: $event, delay: 50})"
            ></v-slider>
          </v-flex>

          <v-flex xs12 v-if="selectedMode!=='appKelvin' && selectedMode!=='appModes'">
            <v-slider
              color="slidercolor"
              :label="labelH"
              :min=0
              :max=359
              thumb-label
              :value="getZonesDp('hue')"
              @input="setZonesDp({dp: 'hue', event: $event, delay: 50})"
            >
            </v-slider>
          </v-flex>

        </v-layout>
      </v-card-text>
    </v-card>
  </v-content>
</template>


<script>
  import { mapGetters, mapActions } from 'vuex';

  export default {
    data () {
      return {};
    }, computed: {
      ...mapGetters([
        'zones', 'getZonesDp', 'selectedZone', 'selectedMode']),
      labelS() {
        return '(S): ' + (this.getZonesDp('saturation') ? this.getZonesDp('saturation').toLocaleString(undefined, { minimumIntegerDigits: 3 }) : '000')
      },
      labelB() {
        return '(B): ' + (this.getZonesDp('brightness') ? this.getZonesDp('brightness').toLocaleString(undefined, { minimumIntegerDigits: 3 }) : '000')
      },
      labelH() {
        return '(H): ' + (this.getZonesDp('hue') ? this.getZonesDp('hue').toLocaleString(undefined, { minimumIntegerDigits: 3 }) : '000')
      }
    },
    methods: {
      ...mapActions(['setZonesDp']), refreshColor () {
        this.setZonesDp({dp: 'on', event: true, delay: false});

        this.setZonesDp({dp: 'rgb', event: this.getZonesDp('rgb'), delay: false});
      },
    },
  };

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



