<template>
  <v-content>
    <v-container fluid grid-list-sm>
      <v-layout
        row
        wrap
      >

        <v-flex xs12 v-if="selectedIBox.value">
          <v-card>
            <v-card-title primary-title>
              <div class="zonecolor--text">
                <h3 class="headline mb-0">{{ selectedIBox.name }}</h3>
                <div>Please select a Zone from <b>{{ selectedIBox.value }}</b> . . .</div>
              </div>
            </v-card-title>
            <v-card-text>
              <v-btn
                v-for="zone in getZonesFromSelectedIBox()"
                :key="zone.zone"
                @click="setSelectedZone(zone)"
                outline block class="my-3 zonecolor--text">{{ zone.name }}
              </v-btn>
            </v-card-text>
          </v-card>
        </v-flex>

      </v-layout>
    </v-container>
  </v-content>
</template>

<script>
  import Vue from 'vue'
  import { mapGetters } from 'vuex';

  import { idToIZS } from '../../helpers'

  export default {
     data () {
      return {
      }
    },
    computed: {
      ...mapGetters([
        'zones', 'selectedIBox', 'selectedZone'])
    },
    methods: {
      getZonesFromSelectedIBox () {
        const tmp = [];
        for (const [index, item] of this.zones.entries()) {
          if (item.instance === this.selectedIBox.value) {
            tmp.push({
              index: index, zone: item.zone, type: item.value.common.type, name: item.value.common.name, dps: item.dps,
            });
          }
        }
        return tmp;
      },
      setSelectedZone (zone) {
        const lastZone = this.selectedZone

        Vue.console.log('setSelectedZone:lastZone ' + lastZone.zone)

        //this.$store.dispatch('setSelectedIBox', { value: '', name: '' });
        this.$store.dispatch('setSelectedZone', zone);

        if(this.selectedIBox.value === this.selectedIBox.old.value ) {
          this.$store.dispatch('updateDpFromClient', {dp: 'on', value: true})
        }

        if(zone.type === 'white') {
          this.$store.dispatch('setSelectedMode', 'appKelvin')
        } else {
          this.$store.dispatch('setSelectedMode', 'appColors')
        }

      },
    },
  };
</script>



