<template>

  <v-content style="padding: 0px">

    <v-layout row wrap>
      <v-flex xs12>
        <app-round-slider
          v-once
          id="slider-white-temperature"
          class="mb-1"
          v-if="selectedMode==='appKelvin' && selectedZone.type==='fullColor'"
          :actual-value="getZonesDp('whiteTemperature')"
          @update-value="setZonesDp({dp: 'whiteTemperature', event: $event, delay: 100});"
          :options="{
        'tooltipFormat': function(e) {return '<div class=slider-tooltip>' + (e.value * 38 + 2700) + '<br>' + ' Kelvin' + '</div>'},
        'min': 0,
        'max': 100,
        'radius': 140,
        'circleShape': 'pie',
        'handleShape': 'dot',
        'startAngle': 315,
        'sliderType': 'min-range',
      }"
        ></app-round-slider>
      </v-flex>
    </v-layout>

    <v-card>
      <v-card-text>
        <v-layout row wrap align-baseline justify-space-around>
          <v-flex
            v-if="selectedMode==='appKelvin' && selectedZone.type!=='white'"
            xs4
            class="text-xs-right"
          >
            <v-btn
              :value="getZonesDp('whiteMode')"
              @click="setZonesDp({dp: 'whiteMode', event: true, delay: false})"
              light
            >White
            </v-btn>
          </v-flex>

          <v-flex
            xs4
            class="text-xs-center"
          >
            <v-btn
              :value="getZonesDp('nightMode')"
              @click="setZonesDp({dp: 'nightMode', event: true, delay: false})"
              dark
              color="nightlight"
            >Night Light
            </v-btn>
          </v-flex>

          <v-flex
            v-if="selectedZone.type!=='white'"
            xs4
            class="text-xs-center"
          >
            <v-btn
              fab
              dark
              :style="{ 'background-color': getZonesDp('rgb')}"
              @click="refreshColor()"
            >
              <v-icon dark>refresh</v-icon>
            </v-btn>
          </v-flex>

        </v-layout>
      </v-card-text>
    </v-card>

  </v-content>

</template>


<script>
  import RoundSlider from '@/components/basics/RoundSlider';
  import { mapGetters, mapActions } from 'vuex';

  export default {
    components: {
      appRoundSlider: RoundSlider,
    }, data () {
      return {};
    }, computed: {
      ...mapGetters([
        'zones', 'getZonesDp', 'selectedZone', 'selectedMode', 'selectedIBox']),
      whiteTemperature () {
        return this.getZonesDp('whiteTemperature') * 38 + 2700;
      },
    }, methods: {
      ...mapActions(['setZonesDp']),
      refreshColor () {
        this.setZonesDp({dp: 'on', event: true, delay: false});
        this.setZonesDp({dp: 'rgb', event: this.getZonesDp('rgb'), delay: false});
      },
    },
  };

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  #slider-white-temperature .rs-handle {
    background-color: #bbdefb;
    padding: 7px;
    border: 2px solid #C2E9F7;
  }

  #slider-white-temperature .rs-handle.rs-focus {
    border-color: #C2E9F7;
  }

  #slider-white-temperature .rs-handle:after {
    border-color: #1e88e5;
    background-color: #1e88e5;
  }

  #slider-white-temperature .rs-border {
    border-color: #1e88e5;
  }

  #slider-white-temperature .rs-range-color {
    background-color: #1e88e5;
  }

  #slider-white-temperature .slider-tooltip {
    color: #1e88e5;
  }
</style>



