<template>
  <v-content style="padding: 0px">
    <v-card>
      <v-card-title class="pb-0">
        <slot></slot>
        <v-spacer></v-spacer>
        <div :style="{'color': colors.hex}">rgb: {{ colors.hex }}</div>
      </v-card-title>
      <v-card-text>
        <transition
          mode="out-in"
          enter-active-class="animated fadeInDown"
          leave-active-class="animated fadeOutDown"
        >
          <app-slider class="center" v-if="picker==='Slider'" v-model="colors"></app-slider>
          <app-compact class="center" v-if="picker==='Compact'" v-model="colors"></app-compact>
          <app-swatches class="center" v-if="picker==='Swatch'" v-model="colors"></app-swatches>
          <app-photoshop class="center" v-if="picker==='Photoshop'" v-model="colors"></app-photoshop>
          <app-chrome class="center" v-if="picker==='Chrome'" v-model="colors"></app-chrome>
        </transition>
      </v-card-text>
    </v-card>
  </v-content>
</template>

<script>
  import { Slider, Swatches, Compact, Material, Photoshop, Chrome } from 'vue-color';
  import { mapGetters } from 'vuex';

  export default {
    components: {
      appSlider: Slider, appSwatches: Swatches, appCompact: Compact, appMaterial: Material, appPhotoshop: Photoshop, appChrome: Chrome,
    }, data () {
      return {};
    }, computed: {
      ...mapGetters([
        'zones', 'selectedZone']), colors: {
        get () {
          return {
            hex: this.zones[this.selectedZone.index].dps[this.selectedZone.zone + '.rgb'].val,
          };
        }, set (options) {
          this.$store.dispatch('updateDpFromClient', {value: options.hex, dp: 'rgb', delay: 300});
        },
      },
    }, props: ['picker'],
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .center {
    width: auto;
  }
</style>



