<template>

  <v-content style="padding: 0px">
    <v-layout row wrap>
      <v-flex xs6>
        <app-round-slider
          v-once
          id="slider-warmer"
          class="mb-1"
          :actual-value="getZonesDp('warmer')"
          @update-value="setZonesDp({dp: 'warmer', event: true, delay: 100});"
          :options="{
                'tooltipFormat': function(e) {return '<div class=slider-tooltip>' + 'warmer' + '</div>'},
                'min': 0,
                'max': 100,
                'radius': 80,
                'circleShape': 'round',
                'handleShape': 'dot',
                'sliderType': 'default',
                'style': 'margin: auto; touch-action: none; z-index: 0;'
              }"
        ></app-round-slider>
      </v-flex>

      <v-flex xs6>
        <app-round-slider
          v-once
          id="slider-cooler"
          class="mb-1"
          :actual-value="getZonesDp('cooler')"
          @update-value="setZonesDp({dp: 'cooler', event: true, delay: 100});"
          :options="{
                'tooltipFormat': function(e) {return '<div class=slider-tooltip>' + 'cooler' + '</div>'},
                'min': 0,
                'max': 100,
                'radius': 80,
                'circleShape': 'round',
                'handleShape': 'dot',
                'sliderType': 'default',
                'style': 'margin: auto; touch-action: none; z-index: 0;'
              }"
        ></app-round-slider>
      </v-flex>
    </v-layout>

    <v-layout row wrap>
      <v-flex xs6>
        <app-round-slider
          v-once
          id="slider-bright-up"
          class="mb-1"
          :actual-value="getZonesDp('brightUp')"
          @update-value="setZonesDp({dp: 'brightUp', event: true, delay: 100});"
          :options="{
                'tooltipFormat': function(e) {return '<div class=slider-tooltip>' + 'bright up' + '</div>'},
                'min': 0,
                'max': 100,
                'radius': 80,
                'circleShape': 'round',
                'handleShape': 'dot',
                'sliderType': 'default',
                'style': 'margin: auto; touch-action: none; z-index: 0;'
              }"
        ></app-round-slider>
      </v-flex>

      <v-flex xs6>
        <app-round-slider
          v-once
          id="slider-bright-down"
          class="mb-1"
          :actual-value="getZonesDp('brightDown')"
          @update-value="setZonesDp({dp: 'brightDown', event: true, delay: 100});"
          :options="{
                'tooltipFormat': function(e) {return '<div class=slider-tooltip>' + 'bright down' + '</div>'},
                'min': 0,
                'max': 100,
                'radius': 80,
                'circleShape': 'round',
                'handleShape': 'dot',
                'sliderType': 'default',
                'style': 'margin: auto; touch-action: none; z-index: 0;'
              }"
        ></app-round-slider>
      </v-flex>
    </v-layout>

    <v-card>
      <v-card-text>
        <v-layout row wrap>
          <v-flex
            xs12
            class="text-xs-center text-sm-center"
          >
            <v-btn
              :value="getZonesDp('maxBright')"
              @click="setZonesDp({dp: 'maxBright', event: true, delay: false})"
              success
              light
              color="maxbright"
            >Max Bright
            </v-btn>
          </v-flex>
        </v-layout>

      </v-card-text>
    </v-card>

  </v-content>

</template>


<script>
  import { mapGetters, mapActions } from 'vuex';
  import RoundSlider from '@/components/basics/RoundSlider';

  export default {
    data () {
      return {};
    }, components: {
      appRoundSlider: RoundSlider,
    }, computed: {
      ...mapGetters([
        'zones', 'getZonesDp'])
    }, methods: {
      ...mapActions(['setZonesDp']), refreshColor () {
        this.setZonesDp({dp: 'on', event: true, delay: false});

        this.setZonesDp({dp: 'rgb', event: this.getZonesDp('rgb'), delay: false});
      },
    },
  };

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  #slider-cooler .rs-handle  {
    background-color: #bbdefb;
    padding: 7px;
    border: 2px solid #C2E9F7;
  }
  #slider-cooler .rs-handle.rs-focus  {
    border-color: #C2E9F7;
  }
  #slider-cooler .rs-handle:after  {
    border-color: #1e88e5;
    background-color: #1e88e5;
  }

  #slider-cooler .rs-range-color  {
    background-color: #1e88e5;
  }
  #slider-cooler .rs-path-color  {
    background-color: #1e88e5;
  }
  #slider-cooler .rs-border  {
    border-color: transparent;
  }

  #slider-cooler .slider-tooltip  {
    color: #1e88e5;
  }


  #slider-warmer .rs-handle  {
    background-color: #ffcdd2;
    padding: 7px;
    border: 2px solid #ffcdd2;
  }
  #slider-warmer .rs-handle.rs-focus  {
    border-color: #ffcdd2;
  }
  #slider-warmer .rs-handle:after  {
    border-color: #c62828;
    background-color: #c62828;
  }

  #slider-warmer .rs-range-color  {
    background-color: #c62828;
  }
  #slider-warmer .rs-path-color  {
    background-color: #c62828;
  }
  #slider-warmer .rs-border  {
    border-color: transparent;
  }

  #slider-warmer .slider-tooltip  {
    color: #c62828;
  }


  #slider-bright-up .rs-handle  {
    background-color: #fbc02d;
    padding: 7px;
    border: 2px solid #fbc02d;
  }
  #slider-bright-up .rs-handle.rs-focus  {
    border-color: #fbc02d;
  }
  #slider-bright-up .rs-handle:after  {
    border-color: #fff9c4;
    background-color: #fff9c4;
  }

  #slider-bright-up .rs-range-color  {
    background-color: #fff9c4;
  }
  #slider-bright-up .rs-path-color  {
    background-color: #fff9c4;
  }
  #slider-bright-up .rs-border  {
    border-color: transparent;
  }

  #slider-bright-up .slider-tooltip  {
    color: #fbc02d;
  }


  #slider-bright-down .rs-handle  {
    background-color: #fff9c4;
    padding: 7px;
    border: 2px solid #fff9c4;
  }
  #slider-bright-down .rs-handle.rs-focus  {
    border-color: #fff9c4;
  }
  #slider-bright-down .rs-handle:after  {
    border-color: #fbc02d;
    background-color: #fbc02d;
  }

  #slider-bright-down .rs-range-color  {
    background-color: #fbc02d;
  }
  #slider-bright-down .rs-path-color  {
    background-color: #fbc02d;
  }
  #slider-bright-down .rs-border  {
    border-color: transparent;
  }

  #slider-bright-down .slider-tooltip  {
    color: #fbc02d;
  }
</style>



