import Vue from 'vue'
import { idToIZS } from '../../helpers'

const state = {
  initialized: false,
  zones: [],
  zoneIndex: []
}

const getters = {
  initialized: state => {
    return state.initialized
  },
  zones: state => {
    return state.zones
  },
  zoneIndex: state => {
    return state.zoneIndex
  },
  getZonesDp: (state, getters) => (dp) => {
    return state.zones[getters.selectedZone.index].dps[getters.selectedZone.zone + '.' + dp].val;
  }
}

const actions = {
  initMslZones ({commit, dispatch}) {
    const fetchZones = (itemZone) => {
      return new Promise((resolve, reject) => {
        this.getters.socket.emit('getStates', itemZone.id + '*', (err, payload) => {
          if (err) {
            reject(err)
          }

          const iz = idToIZS(itemZone.id)
          resolve({ instance: iz.instance, zone: iz.zone, dps: payload, value: itemZone.value })
        })
      })
    }

    new Promise((resolve, reject) => {
      this.getters.socket.emit('getObjectView', 'system', 'channel', {startkey: 'milight-smart-light' + '.', endkey: 'milight-smart-light' + '.\u9999', include_docs: true}, (err, _devices) => {
        Vue.console.log('getObjectView: ' + JSON.stringify(_devices, null, '|'))
        if (err) {
          reject(err)
        }
        resolve(_devices)
      })
    }).then((devices) => {
      Promise.all(devices.rows.map(fetchZones)).then((res) => {
        Vue.console.log('action::initMslZones::Datapoints of Zones: ' + JSON.stringify(res, null, '|'))

        res.forEach((item) => {
          this.getters.socket.emit('subscribe', item.zone + '*')
        })

        commit('initMslZones', res)
      })
    }).catch((err) => {
      Vue.console.log('action::initMslZones::initMslFe: ' + JSON.stringify(err, null, '|'))
    })
  },
  setZonesDp({dispatch}, options) {
    dispatch('updateDpFromClient', {value: options.event, dp: options.dp, delay: options.delay});
  }

}

const mutations = {
  initMslZones (state, zones) {
    for (const [index, item] of zones.entries()) {
      state.zoneIndex[item.zone] = index
    }

    state.zones = zones
    state.initialized = true
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



