<template>
  <v-content>
    <v-container fluid grid-list-sm>
      <v-layout
        row
        wrap
      >
        <v-flex xs12 v-if="selectedZone.type==='white'">
          <app-only-white></app-only-white>
        </v-flex>

        <v-flex xs12>
          <app-white-night></app-white-night>
        </v-flex>

        <v-flex xs12>
          <app-sbh></app-sbh>
        </v-flex>

        <v-flex xs12>
          <app-on-off></app-on-off>
        </v-flex>

      </v-layout>
    </v-container>
  </v-content>
</template>

<script>
  import OnlyWhite from '@/components/kelvin/OnlyWhite';
  import Sbh from '@/components/basics/Sbh';
  import WhiteNight from '@/components/basics/WhiteNight';
  import OnOff from '@/components/basics/OnOff';

  import { mapGetters } from 'vuex';

  export default {
    components: {
      appOnlyWhite: OnlyWhite,
      appSbh: Sbh,
      appWhiteNight: WhiteNight,
      appOnOff: OnOff
    },
    data () {
      return {
      }
    }, computed: {
      ...mapGetters(['selectedZone'])
    },
  };
</script>



