<template>
  <v-content style="padding: 0px">
    <v-card>
      <v-card-text>
        <v-layout row wrap>
          <v-flex xs12>
            <div
              id="slider"
              :style="options.style"
            ></div>
          </v-flex>
        </v-layout>
      </v-card-text>
    </v-card>
  </v-content>
</template>


<script>
  import Vue from 'vue';
  import { mapGetters, mapActions } from 'vuex';

  export default {
    props: ['actualValue','options'],
    data() {
      return {
      }
    },
    created(){
      this.options.style = this.options.style || 'margin: auto auto 42px auto; touch-action: none; z-index: 0;'
    },
    mounted(){
      $(this.$el).find("#slider").roundSlider({
        width: this.options.width || 10,
        circleShape: this.options.circleShape || 'full',
        disabled: this.options.disabled || false,
        editableTooltip: this.options.editableTooltip || false,
        endAngle: this.options.endAngle,
        startAngle: this.options.startAngle,
        handleSize: this.options.handleSize || '+28',
        handleShape: this.options.handleShape || 'round',
        keyboardAction: this.options.keyboardAction || true,
        mouseScrollAction: this.options.mouseScrollAction || true,
        max: this.options.max || 100,
        min: this.options.min || 0,
        radius: this.options.radius || 100,
        readOnly: this.options.readOnly || false,
        showTooltip: this.options.showTooltip || true,
        sliderType: this.options.sliderType || 'default',
        step: this.options.step || 1,

        value: this.actualValue,

        tooltipFormat: this.options.tooltipFormat || function(e) { return e.value },

        change: (e) => {
          Vue.console.log('RoundSlider::change::value: ' + e.value);
          this.$emit('update-value', e.value)
        },
        drag: (e) => {
        Vue.console.log('RoundSlider::drag::value: ' + e.value);
        this.$emit('update-value', e.value)
      }
      })
    },
    beforeDestroy() {
      $(this.$el).find("#slider").roundSlider("destroy")
    },
    watch: {
      actualValue(value) {
        Vue.console.log('RoundSlider::watch::value: ' + value);
        $(this.$el).find("#slider").data("roundSlider").setValue(value)
      }
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



