<template>
  <fullscreen :fullscreen.sync="fullscreen">
    <v-app>

      <v-navigation-drawer
        app
        temporary
        v-model="sideNav"
        style="z-index: 20"
      >
        <v-list>
          <v-list-group v-model="listGroup">
            <v-list-tile slot="item" @click="">
              <v-list-tile-action>
                <v-icon
                  color="menucolor"
                >
                  devices_other
                </v-icon>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title class="menucolor--text">iBoxes</v-list-tile-title>
              </v-list-tile-content>
              <v-list-tile-action>
                <v-icon
                  color="menucolor"
                >
                  keyboard_arrow_down
                </v-icon>
              </v-list-tile-action>
            </v-list-tile>
            <v-list-tile v-for="iBox in getIBoxes()" :key="iBox.value" @click="setSelectedIBox(iBox)">
              <v-list-tile-content>
                <v-list-tile-title class="menucolor--text">{{ iBox.name }}</v-list-tile-title>
              </v-list-tile-content>
              <v-list-tile-action>
                <v-icon
                  color="menucolor"
                >
                  devices_other
                </v-icon>
              </v-list-tile-action>
            </v-list-tile>
          </v-list-group>
        </v-list>

      </v-navigation-drawer>

      <v-toolbar app
                 fixed
                 dark
                 class="menucolor"
      >
        <v-toolbar-side-icon
          @click.native.stop="sideNav = !sideNav"
          class="hidden-sm-and-up"
        >
        </v-toolbar-side-icon>

        <v-spacer></v-spacer>

        <v-toolbar-title>Small FE for milight-smart-light</v-toolbar-title>

        <v-spacer></v-spacer>

        <v-toolbar-items>
          <v-menu v-if="showColorPickersMenu" bottom right>
            <v-btn slot="activator" flat>
              <v-icon left>color_lens</v-icon>
              Picker
            </v-btn>
            <v-list>
              <v-list-tile v-for="colorPicker in colorPickers" :key="colorPicker" @click="setSelectedColorPicker(colorPicker)">
                <v-list-tile-title class="menucolor--text">{{ colorPicker }}</v-list-tile-title>
              </v-list-tile>
            </v-list>
          </v-menu>

          <v-menu bottom right class="hidden-xs-only">
            <v-btn slot="activator" flat>
              <v-icon left>devices_other</v-icon>
              iBoxes
            </v-btn>
            <v-list>
              <v-list-tile v-for="iBox in getIBoxes()" :key="iBox.value" @click="setSelectedIBox(iBox)">
                <v-list-tile-title class="menucolor--text">{{ iBox.name }}</v-list-tile-title>
              </v-list-tile>
            </v-list>
          </v-menu>
        </v-toolbar-items>
      </v-toolbar>

      <v-layout row wrap
                v-if="!selectedMode"
      >
        <v-flex xs12 text-xs-center>
            <img
              src="./assets/milight-smart-light.png"

              style="position: relative; top: 50%; transform: translateY(-50%);
                     display: block; margin: auto; width: 80%;
                     border-radius: 25px;

                     -webkit-box-shadow: 0 0 20px 4px rgba(180,180,180,1) inset;
                     box-shadow: 0 0 20px 4px rgba(180,180,180,1) inset;

                     -webkit-box-shadow: 0 0 30px 7px rgba(180,180,180,1) ;
                     box-shadow: 0 0 30px 7px rgba(180,180,180,1) ;"
            >
        </v-flex>
      </v-layout>

      <main>
        <transition
          mode="out-in"
          enter-active-class="animated slideInUp"
        >
          <component :is="selectedMode"></component>
        </transition>
      </main>

      <v-footer app>
        <v-bottom-nav
          style="margin-left: 0px"
          :value="true"
          :active="selectedMode"
          value="true"
          dark
          class="menucolor"
        >
          <v-btn
            dark
            flat
            value="appColors"
            v-show="selectedZone!=='' && selectedZone.type!=='white'"
            @click="setSelectedMode('appColors')"
          >
            <span>Colors</span>
            <v-icon>color_lens</v-icon>
          </v-btn>

          <v-btn
            dark
            flat
            value="appKelvin"
            v-show="selectedZone!==''"
            @click="setSelectedMode('appKelvin')"
          >
            <span>Kelvin</span>
            <v-icon>wb_sunny</v-icon>
          </v-btn>

          <v-btn
            dark
            flat
            value="appModes"
            v-show="selectedZone!=='' && selectedZone.type!=='white'"
            @click="setSelectedMode('appModes')"
          >
            <span>Modes</span>
            <v-icon>view_module</v-icon>
          </v-btn>

          <v-btn
            dark
            flat
            @click="toggle"
          >
            <span>Fullscreen</span>
            <v-icon>fullscreen</v-icon>
          </v-btn>

        </v-bottom-nav>
      </v-footer>

    </v-app>
  </fullscreen>
</template>

<script>
  import Vue from 'vue';
  import Colors from '@/components/colors/Colors';
  import Kelvin from '@/components/kelvin/Kelvin';
  import Modes from '@/components/modes/Modes';
  import Zones from '@/components/zones/Zones';

  import { mapGetters, mapActions } from 'vuex';

  export default {
    /*created () {
      this.$store.dispatch('initMslSocket');
      this.$store.dispatch('initMslZones');
    },*/
    components: {
      appColors: Colors, appKelvin: Kelvin, appZones: Zones, appModes: Modes,
    }, data () {
      return {
        sideNav: false, listGroup: false, colorPickers: ['Slider', 'Compact', 'Swatch', 'Photoshop', 'Chrome'], fullscreen: false,
      };
    }, computed: {
      ...mapGetters([
        'zones', 'selectedZone', 'selectedMode']), showColorPickersMenu () {
        if (this.selectedZone !== '' && this.selectedZone.type !== 'white' && this.selectedMode !== 'appKelvin' && this.selectedMode !== 'appModes') {
          return true;
        }

        return false;
      },
    }, methods: {
      ...mapActions(['setSelectedMode']),
      getIBoxes () {
        const map = new Map();

        for (const [index, item] of this.zones.entries()) {
          if (!map.has(item.instance)) {

            map.set(item.instance, {
              value: item.instance,
              name: 'iBox::' + item.instance.split('.')[1],
              controllerType: item.value.common.controllerType
            });

          }
        }

        return Array.from(map.values()).sort((a, b) => {
          if (a.name > b.name) {
            return 1;
          }
          if (a.name < b.name) {
            return -1;
          }
          if (a.name === b.name) {
            return 0;
          }
        });
      },
      setSelectedIBox (ibox) {
        this.sideNav = false;
        this.listGroup = false;

        this.$store.dispatch('setSelectedIBox', ibox);
        this.$store.dispatch('setSelectedZone', '');
        this.$store.dispatch('setSelectedMode', 'appZones');
      }, setSelectedColorPicker (picker) {
        this.$store.dispatch('setSelectedColorPicker', picker);
      }, toggle () {
        this.fullscreen = !this.fullscreen;
      },
    },
  };
</script>

<style lang="stylus">
  @import './stylus/main'
</style>



