import Vue from 'vue'
import Vuex from 'vuex'
import initSocket from './modules/initSocket'
import initZones from './modules/initZones'

Vue.use(Vuex)

export const store = new Vuex.Store({
  state: {
    selectedIBox:  {
      name: '',
      value: '',
      old: {
        name: '',
        value: ''
      }
    },
    selectedZone: '',
    selectedMode: '',
    selectedColorPicker: 'Slider'

  },
  getters: {
    selectedIBox: state => {
      return state.selectedIBox
    },
    selectedZone: state => {
      return state.selectedZone
    },
    selectedMode: state => {
      return state.selectedMode
    },
    selectedColorPicker: state => {
      return state.selectedColorPicker
    }
  },
  actions: {
    setSelectedIBox ({ commit }, ibox) {
      commit('setSelectedIBox', ibox)
    },
    setSelectedZone ({ commit }, zone) {
      commit('setSelectedZone', zone)
    },
    setSelectedMode ({ commit }, mode) {
      commit('setSelectedMode', mode)
    },
    setSelectedColorPicker ({ commit }, picker) {
      commit('setSelectedColorPicker', picker)
    }
  },
  mutations: {
    setSelectedIBox (state, ibox) {
      state.selectedIBox.old.value = state.selectedIBox.value
      state.selectedIBox.old.name = state.selectedIBox.name

      state.selectedIBox.name = ibox.name
      state.selectedIBox.value = ibox.value

      state.selectedIBox.controllerType = ibox.controllerType
    },
    setSelectedZone (state, zone) {
      state.selectedZone = zone
    },
    setSelectedMode (state, mode) {
      state.selectedMode = mode
    },
    setSelectedColorPicker (state, picker) {
      state.selectedColorPicker = picker
    }
  },
  modules: {
    initSocket,
    initZones
  }
})



