import Vue from 'vue';
import socket from 'socket.io-client';

import { idToIZS } from '../../helpers';

const state = {
  socket,
  connected: false,
  wait: false,
};

const getters = {
  socket: state => {
    return state.socket;
  }, connected: state => {
    return state.connected;
  }, wait: state => {
    return state.wait;
  },
};

const actions = {
  setSocket ({commit}) {
    let _socket

    Vue.console.log('process.env.NODE_ENV: ' + process.env.NODE_ENV);

    _socket = socket.connect(window.location.hostname + (process.env.NODE_ENV === 'production' ? ':8082' : ':15756'), {
      query: 'key=' + '', transports: ['websocket'], 'reconnection limit': 10000, 'max reconnection attempts': Infinity, reconnection: false,
    });

    /*_socket = socket.connect(window.location.hostname + ':15756', {
      query: 'key=' + '', transports: ['websocket'], 'reconnection limit': 10000, 'max reconnection attempts': Infinity, reconnection: false,
    });*/

    commit('setSocket', _socket);
  },
  initMslSocket ({commit, dispatch}) {
    dispatch('setSocket');

    this.getters.socket.on('connect', () => {
      commit('setConnected', true);
      Vue.console.log('action::initMslSocket: ' + this.getters.connected);
    });

    this.getters.socket.on('stateChange', (id, state) => {
      const IZS = idToIZS(id);

      if (state.ack === true) {
        dispatch('updateDpFromServer', {zone: IZS.zone, dp: id, state: state});
      }
    });
  }, updateDpFromServer ({commit}, options) {
    commit('updateDpFromServer', options);
  }, updateDpFromClient ({commit}, options) {
    Vue.console.log('WAIT: ' + this.getters.wait);

    if (this.getters.wait === true) {
      return;
    }

    if (options.delay !== false) {
      commit('setWait', true);
    }

    Vue.console.log('action::socket::updateDpFromClient: ' + 'vor emit . . .');

    this.getters.socket.emit('setState', this.getters.selectedZone.zone + '.' + options.dp, options.value, (er, res) => {
      Vue.console.log('action::socket::updateDpFromClient: ' + JSON.stringify(res, null, '|'));

      if (options.delay !== false) {
        setTimeout(() => {
          commit('updateDpFromClient');
        }, options.delay);
      }

    });
  },
};

const mutations = {
  setSocket (state, payload) {
    state.socket=payload
  },
  updateDpFromServer (state, options) {
    Vue.console.log('mutation::updateDpFromServer: ' + JSON.stringify(options, null, '|'));

    this.getters.zones[this.getters.zoneIndex[options.zone]].dps[options.dp].val = options.state.val;
  }, updateDpFromClient (state) {
    state.wait = false;
  }, setConnected (state, val) {
    state.connected = val;
  }, setWait (state, val) {
    state.wait = val;
  },
};

export default {
  state, getters, actions, mutations,
};



