<template>

  <v-content>
    <v-container fluid grid-list-sm>
      <v-layout
        row
        wrap
      >
        <v-flex xs12>
          <v-card>
            <v-card-text>

              <v-layout row wrap>
                <v-flex v-if="selectedIBox.controllerType==='v6'"
                  xs8 offset-xs2
                  class="text-xs-center"
                >
                  <v-btn
                    v-for="i in 9"
                    :key="i"
                    fab outline
                    color="modecolor" dark
                    @click="setZonesDp({dp: 'effectMode', event: i})"
                  >M{{ i }}
                  </v-btn>
                </v-flex>

                <v-flex
                  xs12
                  class="text-xs-center">
                  <v-btn
                    fab
                    light color="speedup"
                    @click="setZonesDp({dp: 'effectSpeedUp', event: true})"
                  >
                    <v-icon dark>arrow_upward</v-icon>
                  </v-btn>

                  <v-btn
                    round
                    dark color="menucolor"
                    @click="setZonesDp({dp: 'effectModeNext', event: true})"
                  >
                    Next Effect
                    <v-icon dark>graphic_eq</v-icon>
                  </v-btn>

                  <v-btn
                    fab
                    dark color="speeddown"
                    @click="setZonesDp({dp: 'effectSpeedDown', event: true})"
                  >
                    <v-icon dark>arrow_downward</v-icon>
                  </v-btn>

                </v-flex>

              </v-layout>

            </v-card-text>
          </v-card>
        </v-flex>

        <v-flex xs12>
          <app-sbh></app-sbh>
        </v-flex>

        <v-flex xs12>
          <app-white-night></app-white-night>
        </v-flex>

        <v-flex xs12>
          <app-on-off></app-on-off>
        </v-flex>

      </v-layout>
    </v-container>
  </v-content>


</template>

<script>
  import WhiteNight from '@/components/basics/WhiteNight';
  import Sbh from '@/components/basics/Sbh';
  import OnOff from '@/components/basics/OnOff';


  import { mapGetters, mapActions } from 'vuex';

  export default {
    components: {
      appWhiteNight: WhiteNight,
      appSbh: Sbh,
      appOnOff: OnOff
    }, data () {
      return {};
    }, computed: {
      ...mapGetters(['selectedZone', 'selectedIBox', 'getZonesDp'])
    }, methods: {
      ...mapActions(['setZonesDp'])
    }
  }
</script>



